import React, { useState, useEffect } from "react";
import { Card } from "primereact/card";
import { Button } from "primereact/button";
import { useNavigate } from "react-router-dom";
import { ConfirmDialog } from "primereact/confirmdialog";
import { Toast } from "primereact/toast";
import "../CSS/Ads.css";
import axios from "axios";
import { useAuth } from "../context/AuthContext";

interface RealEstate {
  _id: string;
  title: string;
  description: string;
  price: number;
  location: string;
  propertyType: string;
  bedrooms: number;
  bathrooms: number;
  area: number;
  mainImage: string;
  galleryImages: string[];
  youtubeUrl?: string;
  createdAt: number;
  country: "Lebanon" | "Abidjan";
  type: "realestate";
}

const LebRealEstate: React.FC = () => {
  const [properties, setProperties] = useState<RealEstate[]>([]);
  const [loading, setLoading] = useState(true);
  const [showDeleteDialog, setShowDeleteDialog] = useState(false);
  const [selectedPropertyId, setSelectedPropertyId] = useState<string | null>(
    null
  );
  const navigate = useNavigate();
  const toast = React.useRef<Toast>(null);

  const { user } = useAuth();

  useEffect(() => {
    fetchProperties();
  }, []);

  const fetchProperties = async () => {
    try {
      axios
        .get(`${process.env.REACT_APP_API}/real-estates/by-country/Lebanon`)
        .then((response) => {
          setProperties(response.data);
          console.log(response.data);
        })
        .catch((error) => {
          console.error(error);
        });
    } catch (error) {
      console.error("Error fetching properties:", error);
    } finally {
      setLoading(false);
    }
  };

  const confirmDelete = (e: React.MouseEvent, propertyId: string) => {
    e.stopPropagation();
    setSelectedPropertyId(propertyId);
    setShowDeleteDialog(true);
  };

  const handleDelete = async () => {
    if (!selectedPropertyId) return;

    try {
      await axios.delete(
        `${process.env.REACT_APP_API}/real-estates/${selectedPropertyId}`
      );
      toast.current?.show({
        severity: "success",
        summary: "Success",
        detail: "Property deleted successfully",
        life: 3000,
      });
      fetchProperties();
    } catch (error) {
      console.error("Error deleting property:", error);
      toast.current?.show({
        severity: "error",
        summary: "Error",
        detail: "Failed to delete property",
        life: 3000,
      });
    } finally {
      setShowDeleteDialog(false);
      setSelectedPropertyId(null);
    }
  };

  const renderProperty = (property: RealEstate) => {
    return (
      <div className="ad-card-wrapper" key={property._id}>
        <div
          className="ad-card"
          onClick={() => navigate(`/realestate/${property._id}`)}
        >
          <div className="ad-image-container">
            <img
              src={property.mainImage}
              alt={property.title}
              className="ad-image"
            />
          </div>
          <div className="ad-content">
            <h3 className="ad-title">{property.title}</h3>
            <div className="ad-actions">
              {user && (
                <>
                  <Button
                    icon="pi pi-pencil"
                    className="p-button-rounded p-button-success"
                    onClick={(e) => {
                      e.stopPropagation();
                      navigate(`/edit-realestate/${property._id}`);
                    }}
                  />
                  <Button
                    icon="pi pi-trash"
                    className="p-button-rounded p-button-danger"
                    onClick={(e) => confirmDelete(e, property._id)}
                  />
                </>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  };

  if (loading) {
    return <div>Loading...</div>;
  }

  return (
    <div className="ads-container">
      <Toast ref={toast} />
      <ConfirmDialog
        visible={showDeleteDialog}
        onHide={() => setShowDeleteDialog(false)}
        message="Are you sure you want to delete this property?"
        header="Confirm Delete"
        icon="pi pi-exclamation-triangle"
        accept={handleDelete}
        reject={() => setShowDeleteDialog(false)}
      />
      <div className="ads-grid">{properties.map(renderProperty)}</div>
    </div>
  );
};

export default LebRealEstate;
