import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { Card } from "primereact/card";
import { ConfirmDialog } from "primereact/confirmdialog";
import { Button } from "primereact/button";
import Gallery from "./Gallery";
import { Helmet } from "react-helmet-async";
import { Toast } from "primereact/toast";
import "../CSS/AdsPage.css";
import "primeicons/primeicons.css";
import axios from "axios";

interface Ad {
  id: string;
  title: string;
  text: string;
  imageUrl?: string;
  galleryImages?: string[];
  youtubeUrl?: string;
  createdAt: number;
  type: "ad";
  location: string;
}

const AdsPage: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const [ad, setAd] = useState<Ad | null>(null);
  const [loading, setLoading] = useState(true);
  const [showDeleteDialog, setShowDeleteDialog] = useState(false);
  const toast = React.createRef<Toast>();

  const isArabicText = (text: string) => {
    const arabicPattern = /[\u0600-\u06FF]/;
    return arabicPattern.test(text);
  };

  const getTextDirection = (text: string) => {
    return isArabicText(text) ? "rtl" : "ltr";
  };

  const copyUrl = async () => {
    try {
      const shareUrl = window.location.href;
      if (!ad) {
        await navigator.clipboard.writeText(shareUrl);
        return;
      }
      await navigator.clipboard.writeText(
        `${ad.title} Read more at: ${shareUrl}`
      );
    } catch (err) {
      console.error("Failed to copy URL:", err);
    }
  };

  const getYouTubeVideoId = (url: string) => {
    if (!url) return null;

    const patterns = [
      /(?:youtube\.com\/watch\?v=|youtu\.be\/|youtube\.com\/embed\/)([^&\s]+)/,
      /youtube\.com\/watch\?.*v=([^&\s]+)/,
    ];

    for (const pattern of patterns) {
      const match = url.match(pattern);
      if (match && match[1]) {
        return match[1];
      }
    }

    return null;
  };

  useEffect(() => {
    const fetchAd = async () => {
      if (!id) return;
      try {
        setLoading(true);
        axios
          .get(`${process.env.REACT_APP_API}/ad-spaces/${id}`)
          .then((response) => {
            setAd(response.data);
          })
          .catch((error) => {
            console.error("Error fetching ad:", error);
          });
      } catch (error) {
        console.error("Error fetching ad:", error);
        toast.current?.show({
          severity: "error",
          summary: "Error",
          detail: "Failed to fetch ad details",
          life: 3000,
        });
      } finally {
        setLoading(false);
      }
    };

    fetchAd();
  }, [id]);

  const handleEdit = () => {
    navigate(`/edit-ads/${id}`);
  };

  const handleDelete = async () => {
    if (!id) return;

    try {
      await axios.delete(`${process.env.REACT_APP_API}/ad-spaces/${id}`);
      toast.current?.show({
        severity: "success",
        summary: "Success",
        detail: "Ad deleted successfully",
        life: 3000,
      });
      navigate("/");
    } catch (error) {
      console.error("Error deleting ad:", error);
    }
  };

  return (
    <div className="ads-page-container">
      <Helmet>
        <title>{ad ? ad.title : "Ad Details"}</title>
        <meta name="description" content={ad?.text.substring(0, 160)} />
        <meta property="og:title" content={ad?.title} />
        <meta property="og:description" content={ad?.text.substring(0, 160)} />
        <meta property="og:url" content={window.location.href} />
        <meta property="og:type" content="article" />
        {ad?.imageUrl && (
          <>
            <meta property="og:image" content={ad.imageUrl} />
            <meta property="og:image:width" content="1200" />
            <meta property="og:image:height" content="630" />
          </>
        )}
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:title" content={ad?.title} />
        <meta name="twitter:description" content={ad?.text.substring(0, 160)} />
        {ad?.imageUrl && <meta name="twitter:image" content={ad.imageUrl} />}
      </Helmet>

      <Toast ref={toast} />

      {loading ? (
        <div className="loading-state">Loading...</div>
      ) : !ad ? (
        <div className="error-state">
          <h2>Ad Not Found</h2>
          <p>The ad you're looking for could not be found.</p>
          <Button
            label="Go Back"
            icon="pi pi-arrow-left"
            onClick={() => navigate(-1)}
          />
        </div>
      ) : (
        <>
          <ConfirmDialog
            visible={showDeleteDialog}
            onHide={() => setShowDeleteDialog(false)}
            message="Are you sure you want to delete this ad?"
            header="Confirm Delete"
            icon="pi pi-exclamation-triangle"
            accept={handleDelete}
            reject={() => setShowDeleteDialog(false)}
          />

          <div className="container mx-auto px-4 py-8 max-w-4xl">
            <article className="prose lg:prose-xl mx-auto">
              <h1
                className="text-4xl font-bold mb-6 text-center"
                dir={getTextDirection(ad.title)}
              >
                {ad.title}
              </h1>

              <div className="main-image-container">
                {ad.imageUrl && (
                  <img
                    src={ad.imageUrl}
                    alt={ad.title}
                    className="ad-main-image mx-auto"
                  />
                )}
              </div>

              <div className="social-share-buttons flex gap-4 justify-center mt-4 mb-6">
                <a
                  className="social-btn whatsapp"
                  rel="noreferrer noopener nofollow"
                  href={`whatsapp://send?text=${encodeURIComponent(
                    `${ad.title} Read more at: ${window.location.href}`
                  )}`}
                  title="Share on WhatsApp"
                  aria-label="Share on WhatsApp"
                >
                  <i className="pi pi-whatsapp"></i>
                </a>
                <a
                  className="social-btn instagram"
                  rel="noreferrer noopener nofollow"
                  href={`https://www.instagram.com/sharer/sharer.php?u=${encodeURIComponent(
                    `${ad.title} Read more at: ${window.location.href}`
                  )}`}
                  target="_blank"
                  title="Share on Instagram"
                  aria-label="Share on Instagram"
                >
                  <i className="pi pi-instagram"></i>
                </a>
                <a
                  className="social-btn facebook"
                  rel="noreferrer noopener nofollow"
                  href={`https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(
                    window.location.href
                  )}`}
                  target="_blank"
                  title="Share on Facebook"
                  aria-label="Share on Facebook"
                >
                  <i className="pi pi-facebook"></i>
                </a>
                <button
                  type="button"
                  className="social-btn share-native"
                  aria-label="Share"
                  onClick={() => {
                    const shareData = {
                      url: window.location.href,
                    };
                    navigator.share(shareData).catch(console.error);
                  }}
                >
                  <i className="pi pi-share-alt"></i>
                </button>
                <button
                  className="social-btn copy-link"
                  onClick={copyUrl}
                  title="Copy Link"
                  aria-label="Copy Link"
                >
                  <i className="pi pi-link"></i>
                </button>
              </div>

              <div className="flex justify-center">
                <Card className="w-4/5 my-8">
                  <div
                    className="whitespace-pre-wrap text-justify"
                    dir={getTextDirection(ad.text)}
                    style={{
                      textAlign: isArabicText(ad.text) ? "right" : "left",
                      fontFamily: isArabicText(ad.text)
                        ? "Noto Sans Arabic, sans-serif"
                        : "inherit",
                    }}
                  >
                    {ad.text}
                  </div>
                </Card>
                <br />
                <Card>
                  {ad.galleryImages && ad.galleryImages.length > 0 && (
                    <div className="mt-8">
                      <h2 className="text-2xl font-bold mb-4">Gallery</h2>
                      <Gallery images={ad.galleryImages} isEditing={false} />
                    </div>
                  )}

                  {ad.youtubeUrl && (
                    <div className="mt-8">
                      <h2 className="text-2xl font-bold mb-4">Video</h2>
                      <div className="relative w-full h-[500px]">
                        {getYouTubeVideoId(ad.youtubeUrl) ? (
                          <iframe
                            src={`https://www.youtube.com/embed/${getYouTubeVideoId(
                              ad.youtubeUrl
                            )}`}
                            title="Advertisement Video"
                            frameBorder="0"
                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                            allowFullScreen
                            className="w-full h-full"
                          />
                        ) : (
                          <div className="w-full h-full flex items-center justify-center bg-gray-100">
                            <p className="text-gray-500">Invalid YouTube URL</p>
                          </div>
                        )}
                      </div>
                    </div>
                  )}
                </Card>
              </div>
            </article>
          </div>
        </>
      )}
    </div>
  );
};

export default AdsPage;
