import React, { useState, useEffect } from "react";
import { Card } from "primereact/card";
import { Button } from "primereact/button";
import { useNavigate } from "react-router-dom";
import { ConfirmDialog } from "primereact/confirmdialog";
import { Toast } from "primereact/toast";
import "../CSS/Cars.css";
import axios from "axios";
import { useAuth } from "../context/AuthContext";

interface Car {
  _id: string;
  title: string;
  text: string;
  imageUrl?: string;
  galleryImages: string[];
  price: number;
  model: string;
  year: number;
  mileage: number;
  createdAt: number;
  country: "Lebanon" | "Abidjan";
  type: "car";
}

const AbidjanCars: React.FC = () => {
  const [cars, setCars] = useState<Car[]>([]);
  const [loading, setLoading] = useState(true);
  const [showDeleteDialog, setShowDeleteDialog] = useState(false);
  const [selectedCarId, setSelectedCarId] = useState<string | null>(null);
  const navigate = useNavigate();
  const toast = React.useRef<Toast>(null);

    const { user } = useAuth();
  

  useEffect(() => {
    fetchCars();
  }, []);

  const fetchCars = async () => {
    try {
      axios
        .get(process.env.REACT_APP_API + "/cars/by-country/Abidjan")
        .then((response) => {
          setCars(response.data);
        })
        .catch((error) => {
          console.error(error);
        });
    } catch (error) {
      toast.current?.show({
        severity: "error",
        summary: "Error",
        detail: "Failed to fetch cars",
        life: 3000,
      });
    } finally {
      setLoading(false);
    }
  };

  const confirmDelete = (e: React.MouseEvent, carId: string) => {
    e.stopPropagation();
    setSelectedCarId(carId);
    setShowDeleteDialog(true);
  };

  const handleDelete = async () => {
    if (!selectedCarId) return;

    try {
      await axios.delete(process.env.REACT_APP_API + `/cars/${selectedCarId}`);
      toast.current?.show({
        severity: "success",
        summary: "Success",
        detail: "Car deleted successfully",
        life: 3000,
      });
      fetchCars();
    } catch (error) {
      toast.current?.show({
        severity: "error",
        summary: "Error",
        detail: "Failed to delete car",
        life: 3000,
      });
    } finally {
      setShowDeleteDialog(false);
      setSelectedCarId(null);
    }
  };

  if (loading) {
    return <div>Loading...</div>;
  }

  return (
    <div className="cars-container">
      <Toast ref={toast} />
      <ConfirmDialog
        visible={showDeleteDialog}
        onHide={() => setShowDeleteDialog(false)}
        message="Are you sure you want to delete this car?"
        header="Confirm Delete"
        icon="pi pi-exclamation-triangle"
        accept={handleDelete}
        reject={() => setShowDeleteDialog(false)}
      />
      <div className="cars-header">
        <h1>Abidjan Cars</h1>
      </div>

      <div className="cars-grid">
        {cars.map((car) => (
          <div
            key={car._id}
            className="car-card-wrapper"
            onClick={(e) => {
              e.preventDefault();
              navigate(`/car/${car._id}`);
            }}
          >
            <Card className="car-card">
              {car.imageUrl && (
                <div className="car-image-container">
                  <img
                    src={car.imageUrl}
                    alt={car.title}
                    className="car-image"
                  />
                </div>
              )}
              <div className="car-content">
                <h2 className="car-title">{car.title}</h2>
                {user && (
                  <div className="car-actions">
                    <Button
                      icon="pi pi-pencil"
                      onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        navigate(`/edit-car/${car._id}`);
                      }}
                      className="p-button-rounded p-button-success p-mr-2"
                    />
                    <Button
                      icon="pi pi-trash"
                      className="p-button-rounded p-button-danger"
                      onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        confirmDelete(e, car._id);
                      }}
                    />
                  </div>
                )}
              </div>
            </Card>
          </div>
        ))}
      </div>
    </div>
  );
};

export default AbidjanCars;
