// src/components/AddCar.tsx
import React, { useState, useEffect, useRef } from "react";
import { Card } from "primereact/card";
import { Button } from "primereact/button";
import { InputText } from "primereact/inputtext";
import { InputTextarea } from "primereact/inputtextarea";
import { InputNumber } from "primereact/inputnumber";
import { useNavigate, useParams } from "react-router-dom";
import { ConfirmDialog, confirmDialog } from "primereact/confirmdialog";
import { Toast } from "primereact/toast";
import { RadioButton } from "primereact/radiobutton";
import { Checkbox, CheckboxChangeEvent } from "primereact/checkbox";
import Gallery from "../components/Gallery";
import "../CSS/App.css";
import { DocumentData } from "firebase/firestore";
import { Cloudinary } from "@cloudinary/url-gen";
import { uploadToCloudinary } from "../utils/cloudinary";
import axios from "axios";

interface Car extends DocumentData {
  id: string;
  title: string;
  text: string;
  imageUrl?: string;
  galleryImages: string[];
  youtubeUrl?: string;
  price: number;
  model: string;
  year: number;
  mileage: number;
  createdAt: number;
  country: "Lebanon" | "Abidjan";
  type: "car";
  showInFlipcard: boolean;
}

const AddCar: React.FC = () => {
  const [cars, setCars] = useState<Car[]>([]);
  const [title, setTitle] = useState("");
  const [text, setText] = useState("");
  const [imageFile, setImageFile] = useState<File | null>(null);
  const [imageUrl, setImageUrl] = useState<string>("");
  const [galleryImages, setGalleryImages] = useState<string[]>([]);
  const [youtubeUrl, setYoutubeUrl] = useState("");
  const [price, setPrice] = useState<number>(0);
  const [model, setModel] = useState("");
  const [year, setYear] = useState<number>(new Date().getFullYear());
  const [mileage, setMileage] = useState<number>(0);
  const [country, setCountry] = useState<"Lebanon" | "Abidjan">("Lebanon");
  const [showInFlipcard, setShowInFlipcard] = useState(false);
  const [editMode, setEditMode] = useState(false);
  const [selectedCarId, setSelectedCarId] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string>("");
  const [uploading, setUploading] = useState(false);

  const navigate = useNavigate();
  const { id } = useParams<{ id?: string }>();
  const fileInputRef = React.useRef<HTMLInputElement>(null);
  const galleryInputRef = React.useRef<HTMLInputElement>(null);
  const toast = useRef<Toast>(null);

  const handleUploadClick = () => {
    fileInputRef.current?.click();
  };

  const handleGalleryUploadClick = () => {
    galleryInputRef.current?.click();
  };

  useEffect(() => {
    fetchCars();
  }, []);

  useEffect(() => {
    if (id) {
      const fetchCar = async () => {
        try {
          const response = await axios.get(
            `${process.env.REACT_APP_API}/cars/${id}`
          );
          if (response.data) {
            const car = response.data;
            setTitle(car.title);
            setText(car.text);
            setImageUrl(car.imageUrl || "");
            setGalleryImages(car.galleryImages || []);
            setYoutubeUrl(car.youtubeUrl || "");
            setPrice(car.price);
            setModel(car.model);
            setYear(car.year);
            setMileage(car.mileage);
            setCountry(car.country);
            setShowInFlipcard(car.showInFlipcard);
            setEditMode(true);
            setSelectedCarId(id);
          } else {
            console.error("Car not found for the given ID");
          }
        } catch (error) {
          console.error("Error fetching car:", error);
        } finally {
          setLoading(false);
        }
      };
      fetchCar();
    }
  }, [id]);

  const fetchCars = async () => {
    try {
      const response = await axios.get(process.env.REACT_APP_API + "/cars");
      const fetchedCars: Car[] = response.data;
      setCars(fetchedCars);
    } catch (error) {
      console.error("Error fetching cars:", error);
    }
  };

  const validateImage = (file: File): boolean => {
    const MAX_SIZE = 5 * 1024 * 1024; // 5MB
    const ALLOWED_TYPES = [
      "image/jpeg",
      "image/png",
      "image/gif",
      "image/webp",
    ];

    if (!ALLOWED_TYPES.includes(file.type)) {
      setError(
        "Invalid file type. Please upload a JPEG, PNG, GIF, or WebP image."
      );
      return false;
    }

    if (file.size > MAX_SIZE) {
      setError("File size too large. Please upload an image smaller than 5MB.");
      return false;
    }

    return true;
  };

  const handleImageUpload = async (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    const file = event.target.files?.[0];
    if (!file) return;

    if (!validateImage(file)) return;

    try {
      setUploading(true);
      const uploadedUrl = await uploadToCloudinary(file);
      setImageUrl(uploadedUrl);
    } catch (error) {
      console.error("Error uploading image:", error);
      setError("Failed to upload image. Please try again.");
    } finally {
      setUploading(false);
    }
  };

  const handleGalleryUpload = async (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    const files = event.target.files;
    if (!files) return;

    const validFiles: File[] = [];
    for (let i = 0; i < files.length; i++) {
      if (validateImage(files[i])) {
        validFiles.push(files[i]);
      }
    }

    try {
      setUploading(true);
      const uploadPromises = validFiles.map((file) => uploadToCloudinary(file));
      const uploadedUrls = await Promise.all(uploadPromises);
      setGalleryImages([...galleryImages, ...uploadedUrls]);
    } catch (error) {
      console.error("Error uploading gallery images:", error);
      setError("Failed to upload some gallery images. Please try again.");
    } finally {
      setUploading(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError("");

    try {
      if (!title || !text || !model || !year || !mileage || !price) {
        throw new Error("Please fill in all required fields");
      }

      if (editMode && selectedCarId) {
        await axios.put(`${process.env.REACT_APP_API}/cars/${selectedCarId}`, {
          title,
          text,
          imageUrl,
          galleryImages,
          youtubeUrl,
          price,
          model,
          year,
          mileage,
          country,
          showInFlipcard,
        });

        toast.current?.show({
          severity: "success",
          summary: "Success",
          detail: "Car updated successfully",
          life: 3000,
        });
      } else {
        // const carsRef = collection(db, "cars");
        // const querySnapshot = await getDocs(
        //   query(carsRef, where("showInFlipcard", "==", true))
        // );

        // if (showInFlipcard && querySnapshot.size >= 5) {
        //   toast.current?.show({
        //     severity: "error",
        //     summary: "Error",
        //     detail:
        //       "Maximum limit of 5 flipcards reached. Please uncheck some posts first.",
        //     life: 5000,
        //   });
        //   setShowInFlipcard(false);
        //   return;
        // }

        // await addDoc(carsRef, {
        //   title,
        //   text,
        //   imageUrl,
        //   galleryImages,
        //   youtubeUrl,
        //   price,
        //   model,
        //   year,
        //   mileage,
        //   country,
        //   showInFlipcard,
        //   type: "car",
        //   createdAt: Date.now(),
        // });

        await axios.post(`${process.env.REACT_APP_API}/cars`, {
          title,
          text,
          imageUrl,
          galleryImages,
          youtubeUrl,
          price,
          model,
          year,
          mileage,
          country,
          showInFlipcard,
        });

        toast.current?.show({
          severity: "success",
          summary: "Success",
          detail: "Car added successfully",
          life: 3000,
        });

        fetchCars();
      }
      if (country === "Lebanon") {
        navigate("/lebanon-cars");
      } else {
        navigate("/abidjan-cars");
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : "An error occurred");
      toast.current?.show({
        severity: "error",
        summary: "Error",
        detail: err instanceof Error ? err.message : "An error occurred",
        life: 3000,
      });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="add-car-container">
      <Toast ref={toast} />
      <Card
        title={editMode ? "Edit Car" : "Add New Car"}
        className="add-car-card"
      >
        <form onSubmit={handleSubmit} className="p-fluid">
          <div className="p-field mb-3">
            <label htmlFor="title">Title</label>
            <InputText
              id="title"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              required
            />
          </div>

          <div className="p-field mb-3">
            <label htmlFor="model">Model</label>
            <InputText
              id="model"
              value={model}
              onChange={(e) => setModel(e.target.value)}
              required
            />
          </div>

          <div className="p-field mb-3">
            <label htmlFor="year">Year</label>
            <InputNumber
              id="year"
              value={year}
              onChange={(e) => setYear(e.value || new Date().getFullYear())}
              required
            />
          </div>

          <div className="p-field mb-3">
            <label htmlFor="mileage">Mileage (km)</label>
            <InputNumber
              id="mileage"
              value={mileage}
              onChange={(e) => setMileage(e.value || 0)}
              required
            />
          </div>

          <div className="p-field mb-3">
            <label htmlFor="price">Price ($)</label>
            <InputNumber
              id="price"
              value={price}
              onChange={(e) => setPrice(e.value || 0)}
              mode="currency"
              currency="USD"
              required
            />
          </div>

          <div className="p-field mb-3">
            <label htmlFor="description">Description</label>
            <InputTextarea
              id="description"
              value={text}
              onChange={(e) => setText(e.target.value)}
              rows={5}
              required
            />
          </div>

          <div className="p-field mb-3">
            <label>Main Image</label>
            <div className="p-inputgroup">
              <Button
                type="button"
                icon="pi pi-image"
                onClick={handleUploadClick}
                loading={uploading}
              />
              <input
                type="file"
                ref={fileInputRef}
                style={{ display: "none" }}
                onChange={handleImageUpload}
                accept="image/*"
              />
            </div>
            {imageUrl && (
              <img
                src={imageUrl}
                alt="Preview"
                style={{ maxWidth: "200px", marginTop: "10px" }}
              />
            )}
          </div>

          <div className="p-field mb-3">
            <label>Gallery Images</label>
            <div className="p-inputgroup">
              <Button
                type="button"
                icon="pi pi-images"
                onClick={handleGalleryUploadClick}
                loading={uploading}
              />
              <input
                type="file"
                ref={galleryInputRef}
                style={{ display: "none" }}
                onChange={handleGalleryUpload}
                accept="image/*"
                multiple
              />
            </div>
            {galleryImages.length > 0 && (
              <Gallery
                images={galleryImages}
                isEditing={true}
                onImageUpload={handleGalleryUpload}
                onImageRemove={(index) => {
                  const newGalleryImages = [...galleryImages];
                  newGalleryImages.splice(index, 1);
                  setGalleryImages(newGalleryImages);
                }}
              />
            )}
          </div>

          <div className="p-field mb-3">
            <label>YouTube URL</label>
            <InputText
              id="youtubeUrl"
              value={youtubeUrl}
              onChange={(e) => setYoutubeUrl(e.target.value)}
            />
          </div>

          <div className="p-field-checkbox" style={{ marginTop: "1rem" }}>
            <Checkbox
              inputId="showInFlipcard"
              checked={showInFlipcard}
              onChange={(e) => setShowInFlipcard(e.target.checked ?? false)}
            />
            <label
              htmlFor="showInFlipcard"
              className="p-checkbox-label"
              style={{ marginLeft: "0.5rem" }}
            >
              Show in Home Flipcard Section
            </label>
          </div>

          <div className="p-field mb-3">
            <label>Show in Flipcard</label>
            <div className="radio-group">
              <div className="radio-item">
                <RadioButton
                  inputId="showInFlipcard-yes"
                  name="showInFlipcard"
                  value={true}
                  onChange={(e) => setShowInFlipcard(e.value)}
                  checked={showInFlipcard}
                />
                <label htmlFor="showInFlipcard-yes" className="radio-label">
                  Yes
                </label>
              </div>
              <div className="radio-item">
                <RadioButton
                  inputId="showInFlipcard-no"
                  name="showInFlipcard"
                  value={false}
                  onChange={(e) => setShowInFlipcard(e.value)}
                  checked={!showInFlipcard}
                />
                <label htmlFor="showInFlipcard-no" className="radio-label">
                  No
                </label>
              </div>
            </div>
          </div>

          <div className="p-field mb-3">
            <label>Country</label>
            <div className="radio-group">
              <div className="radio-item">
                <RadioButton
                  inputId="Lebanon"
                  name="country"
                  value="Lebanon"
                  onChange={(e) => setCountry(e.value)}
                  checked={country === "Lebanon"}
                />
                <label htmlFor="Lebanon" className="radio-label">
                  Lebanon
                </label>
              </div>
              <div className="radio-item">
                <RadioButton
                  inputId="Abidjan"
                  name="country"
                  value="Abidjan"
                  onChange={(e) => setCountry(e.value)}
                  checked={country === "Abidjan"}
                />
                <label htmlFor="Abidjan" className="radio-label">
                  Abidjan
                </label>
              </div>
            </div>
          </div>

          {error && (
            <div className="p-field mb-3">
              <small className="p-error">{error}</small>
            </div>
          )}

          <Button
            type="submit"
            label={editMode ? "Update Car" : "Add Car"}
            className="p-button-primary"
            loading={loading}
          />
        </form>
      </Card>
    </div>
  );
};

export default AddCar;
