// src/components/AddBlog.tsx
import React, { useState, useEffect, useRef } from "react";
import { Card } from "primereact/card";
import { Button } from "primereact/button";
import { InputText } from "primereact/inputtext";
import { InputTextarea } from "primereact/inputtextarea";
import { Checkbox } from "primereact/checkbox";
import { useNavigate, useParams } from "react-router-dom";
import { ConfirmDialog, confirmDialog } from "primereact/confirmdialog";
import { Toast } from "primereact/toast";
import Gallery from "../components/Gallery";
import "../CSS/App.css";
import { DocumentData } from "firebase/firestore";
import { v4 as uuidv4 } from "uuid";
import { Cloudinary } from "@cloudinary/url-gen";
import axios from "axios";

interface BlogPost extends DocumentData {
  id: string;
  title: string;
  text: string;
  imageUrl?: string;
  galleryImages: string[];
  youtubeUrl?: string;
  showInHome: boolean;
  createdAt: number;
  isSliding?: boolean;
}

const AddBlog: React.FC = () => {
  const [posts, setPosts] = useState<BlogPost[]>([]);
  const [title, setTitle] = useState("");
  const [text, setText] = useState("");
  const [imageFile, setImageFile] = useState<File | null>(null);
  const [imageUrl, setImageUrl] = useState<string>("");
  const [galleryImages, setGalleryImages] = useState<string[]>([]);
  const [youtubeUrl, setYoutubeUrl] = useState("");
  const [editMode, setEditMode] = useState(false);
  const [selectedPostId, setSelectedPostId] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string>("");
  const [uploading, setUploading] = useState(false);
  const [isSliding, setIsSliding] = useState(false);
  const [showInHome, setShowInHome] = useState(false);

  const navigate = useNavigate();
  const { id } = useParams<{ id?: string }>();
  const fileInputRef = React.useRef<HTMLInputElement>(null);
  const galleryInputRef = React.useRef<HTMLInputElement>(null);

  const handleUploadClick = () => {
    fileInputRef.current?.click();
  };

  const handleGalleryUploadClick = () => {
    galleryInputRef.current?.click();
  };

  useEffect(() => {
    fetchPosts();
  }, []);

  useEffect(() => {
    if (id) {
      const fetchPost = async () => {
        const response = await axios.get(
          `${process.env.REACT_APP_API}/posts/${id}`
        );
        const postData = response.data as BlogPost;
        setTitle(postData.title);
        setText(postData.text);
        setImageUrl(postData.imageUrl || "");
        setGalleryImages(postData.galleryImages);
        setYoutubeUrl(postData.youtubeUrl || "");
        setIsSliding(postData.isSliding || false);
        setShowInHome(postData.showInHome || false);
        setSelectedPostId(id);
        setEditMode(true);
      };
      fetchPost();
    }
  }, [id]);

  const fetchPosts = async () => {
    try {
      const response = await axios.get(`${process.env.REACT_APP_API}/posts`);
      setPosts(response.data);
    } catch (error) {
      console.error("Error fetching posts:", error);
    }
  };

  const validateImage = (file: File): boolean => {
    const MAX_SIZE = 5 * 1024 * 1024; // 5MB
    const ALLOWED_TYPES = [
      "image/jpeg",
      "image/png",
      "image/gif",
      "image/webp",
    ];

    if (!ALLOWED_TYPES.includes(file.type)) {
      setError("Please upload only JPG, PNG, GIF, or WebP images");
      return false;
    }

    if (file.size > MAX_SIZE) {
      setError("Image size should be less than 5MB");
      return false;
    }

    return true;
  };

  const validateYoutubeUrl = (url: string) => {
    if (!url) return true; // Empty URL is valid (optional field)
    const patterns = [
      /^https?:\/\/(?:www\.)?youtube\.com\/watch\?v=[^&\s]+/,
      /^https?:\/\/(?:www\.)?youtu\.be\/[^&\s]+/,
      /^https?:\/\/(?:www\.)?youtube\.com\/embed\/[^&\s]+/,
    ];
    return patterns.some((pattern) => pattern.test(url));
  };

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setError("");
    if (e.target.files && e.target.files[0]) {
      const file = e.target.files[0];
      if (!validateImage(file)) return;

      setImageFile(file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setImageUrl(reader.result as string);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleGalleryImageChange = async (
    e: React.ChangeEvent<HTMLInputElement>
  ) => {
    if (e.target.files && e.target.files.length > 0) {
      setLoading(true);
      try {
        const uploadPromises = Array.from(e.target.files).map((file) =>
          uploadImage(file)
        );
        const uploadedUrls = await Promise.all(uploadPromises);
        setGalleryImages((prev) => [...prev, ...uploadedUrls]);
      } catch (error) {
        console.error("Error uploading gallery images:", error);
      }
      setLoading(false);
    }
  };

  const handleYoutubeUrlChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const url = e.target.value;
    setYoutubeUrl(url);

    if (url && !validateYoutubeUrl(url)) {
      setError(
        "Please enter a valid YouTube URL (e.g., https://www.youtube.com/watch?v=...)"
      );
    } else {
      setError("");
    }
  };

  const uploadImage = async (file: File): Promise<string> => {
    setUploading(true);
    try {
      const formData = new FormData();
      formData.append("file", file);
      formData.append("upload_preset", "ml_default");
      formData.append("cloud_name", "dzuppkxqa");

      const response = await fetch(
        `https://api.cloudinary.com/v1_1/dzuppkxqa/image/upload`,
        {
          method: "POST",
          body: formData,
        }
      );

      if (!response.ok) {
        throw new Error("Failed to upload image");
      }

      const data = await response.json();
      return data.secure_url;
    } catch (error) {
      console.error("Error uploading image:", error);
      setError("Failed to upload image. Please try again.");
      throw error;
    } finally {
      setUploading(false);
    }
  };

  const removeGalleryImage = (index: number) => {
    setGalleryImages((prev) => prev.filter((_, i) => i !== index));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError("");

    try {
      let uploadedImageUrl = imageUrl;

      if (imageFile) {
        uploadedImageUrl = await uploadImage(imageFile);
      }

      const postData = {
        title,
        text,
        imageUrl: uploadedImageUrl || "",
        galleryImages: galleryImages || [],
        youtubeUrl: youtubeUrl.trim(),
        showInHome,
        createdAt: Date.now(),
        isSliding,
      };

      if (editMode && selectedPostId) {
        // const postRef = doc(db, "posts", selectedPostId);
        // await updateDoc(postRef, postData);
        await axios.put(
          `${process.env.REACT_APP_API}/posts/${selectedPostId}`,
          postData
        );
      } else {
        // const postsRef = collection(db, "posts");
        // await addDoc(postsRef, postData);
        await axios.post(`${process.env.REACT_APP_API}/posts`, postData);
      }

      setLoading(false);
      navigate("/blog");
    } catch (error) {
      console.error("Error saving post:", error);
      setLoading(false);
    }
  };

  const handleDelete = async (postId: string) => {
    try {
      await axios.delete(`${process.env.REACT_APP_API}/posts/${postId}`);
      await fetchPosts();
      navigate("/");
    } catch (error) {
      console.error("Error deleting post:", error);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-b from-gray-50 to-gray-100 py-8">
      <Card className="max-w-4xl mx-auto shadow-lg">
        <div className="flex flex-col items-center p-6">
          <h1 className="text-3xl font-bold mb-8 text-gray-800">
            {editMode ? "Edit Blog Post" : "Create New Blog Post"}
          </h1>

          {error && (
            <div className="w-full mb-4 p-4 bg-red-100 text-red-700 rounded-lg">
              {error}
            </div>
          )}

          {/* Main Image Upload */}
          <div className="field w-full max-w-3xl mb-8">
            <label
              htmlFor="image"
              className="block text-lg font-medium text-gray-700 mb-2"
            ></label>
            <div className="flex flex-col items-center p-6 border-2 border-dashed border-gray-300 rounded-lg hover:border-blue-500 transition-colors">
              <input
                type="file"
                id="image"
                ref={fileInputRef}
                accept="image/jpeg,image/png,image/gif,image/webp"
                onChange={handleImageChange}
                className="hidden"
              />
              {uploading ? (
                <div className="flex items-center justify-center">
                  <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500"></div>
                  <span className="ml-2">Uploading...</span>
                </div>
              ) : imageUrl ? (
                <div
                  className="relative group cursor-pointer flex flex-col items-center"
                  onClick={handleUploadClick}
                >
                  {imageUrl && (
                    <img
                      src={imageUrl}
                      alt="Preview"
                      width="250"
                      height="200"
                      style={{ display: "block" }}
                      className="rounded-lg"
                    />
                  )}
                  <div className="absolute inset-0 bg-black bg-opacity-50 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity rounded-lg">
                    <span className="text-white text-sm">Click to change</span>
                  </div>
                </div>
              ) : (
                <div className="space-y-4">
                  <i className="pi pi-image text-4xl text-gray-400"></i>
                  <div className="flex flex-col items-center gap-2">
                    <Button
                      label="Choose Image"
                      icon="pi pi-upload"
                      className="p-button-rounded p-button-outlined"
                      onClick={handleUploadClick}
                    />
                    <p className="text-sm text-gray-500">
                      JPG, PNG, GIF or WebP (max. 5MB)
                    </p>
                  </div>
                </div>
              )}
            </div>
          </div>

          {/* Gallery Section */}
          <div className="w-full max-w-3xl mb-8">
            <h2 className="text-xl font-semibold mb-4 text-gray-700">
              Gallery
            </h2>
            <input
              type="file"
              ref={galleryInputRef}
              accept="image/*"
              onChange={handleGalleryImageChange}
              className="hidden"
              multiple
            />
            <Gallery
              images={galleryImages}
              onImageUpload={handleGalleryImageChange}
              onImageRemove={removeGalleryImage}
              isEditing={true}
            />
          </div>

          {/* YouTube Link Input */}
          <div className="field w-full max-w-3xl mb-8">
            <label
              htmlFor="youtube"
              className="block text-lg font-medium text-gray-700 mb-2"
            >
              YouTube Video Link (Optional)
            </label>
            <div className="p-inputgroup">
              <span className="p-inputgroup-addon">
                <i className="pi pi-youtube text-red-500"></i>
              </span>
              <InputText
                id="youtube"
                value={youtubeUrl}
                onChange={handleYoutubeUrlChange}
                placeholder="Enter YouTube video URL"
                className={`w-full ${
                  !validateYoutubeUrl(youtubeUrl) ? "p-invalid" : ""
                }`}
              />
            </div>
            <small className="text-gray-500">
              Paste the full YouTube video URL (e.g.,
              https://www.youtube.com/watch?v=...)
            </small>
            {youtubeUrl && !validateYoutubeUrl(youtubeUrl) && (
              <small className="p-error block mt-1">
                Please enter a valid YouTube URL
              </small>
            )}
          </div>

          {/* Show in Home Checkbox */}
          <div className="field-checkbox mb-4">
            <Checkbox
              inputId="showInHome"
              checked={showInHome}
              onChange={(e) => setShowInHome(e.checked ?? false)}
            />
            <label htmlFor="showInHome" className="ml-2">
              Show in Home (limited to 3 posts)
            </label>
          </div>

          {/* Title Input */}
          <div className="field w-full max-w-3xl mb-6">
            <span className="p-float-label">
              <InputText
                id="title"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                className="w-full p-3 text-lg"
              />
              <label htmlFor="title" className="text-gray-600">
                Title
              </label>
            </span>
          </div>

          {/* Text Content */}
          <div className="field w-full max-w-3xl mb-8">
            <span className="p-float-label">
              <InputTextarea
                id="text"
                value={text}
                onChange={(e) => setText(e.target.value)}
                rows={8}
                className="w-full p-3 text-lg"
              />
              <label htmlFor="text" className="text-gray-600">
                Content
              </label>
            </span>
          </div>

          {/* Action Buttons */}
          <div className="flex gap-4 mt-6">
            <Button
              label={editMode ? "Update" : "Publish"}
              icon="pi pi-send"
              onClick={handleSubmit}
              className="px-6 py-3 bg-blue-600 hover:bg-blue-700 transition-colors"
              disabled={loading}
            />
            {editMode && (
              <Button
                label="Delete"
                icon="pi pi-trash"
                onClick={() => handleDelete(selectedPostId as string)}
                className="px-6 py-3 bg-red-500 hover:bg-red-600 transition-colors"
              />
            )}
          </div>
        </div>
      </Card>
      <ConfirmDialog />
    </div>
  );
};

export default AddBlog;
