import React, { useState, useEffect } from "react";
import { Card } from "primereact/card";
import { Button } from "primereact/button";
import { useNavigate } from "react-router-dom";
import { ConfirmDialog } from "primereact/confirmdialog";
import { Toast } from "primereact/toast";
import "../CSS/Blog.css";
import axios from "axios";
import { useAuth } from "../context/AuthContext";

interface BlogPost {
  _id: string;
  title: string;
  text: string;
  imageUrl?: string;
  galleryImages: string[];
  createdAt: number;
}

const Blog: React.FC = () => {
  const [posts, setPosts] = useState<BlogPost[]>([]);
  const [loading, setLoading] = useState(true);
  const [showDeleteDialog, setShowDeleteDialog] = useState(false);
  const [selectedPostId, setSelectedPostId] = useState<string | null>(null);
  const navigate = useNavigate();
  const toast = React.useRef<Toast>(null);

  const { user } = useAuth();

  useEffect(() => {
    fetchPosts();
  }, []);

  const fetchPosts = async () => {
    try {
      axios
        .get(process.env.REACT_APP_API + "/posts")
        .then((response) => {
          setPosts(response.data);
        })
        .catch((error) => {
          console.error(error);
        });
    } catch (error) {
      console.error("Error fetching posts:", error);
      toast.current?.show({
        severity: "error",
        summary: "Error",
        detail: "Failed to load posts",
        life: 3000,
      });
    } finally {
      setLoading(false);
    }
  };

  const handleBlogClick = (postId: string) => {
    navigate(`/blog/${postId}`);
  };

  const handleEdit = (e: React.MouseEvent, postId: string) => {
    e.stopPropagation();
    navigate(`/edit-blog/${postId}`);
  };

  const confirmDelete = (e: React.MouseEvent, postId: string) => {
    e.stopPropagation();
    setSelectedPostId(postId);
    setShowDeleteDialog(true);
  };

  const handleDelete = async () => {
    if (!selectedPostId) return;

    try {
      await axios.delete(
        process.env.REACT_APP_API + `/posts/${selectedPostId}`
      );
      toast.current?.show({
        severity: "success",
        summary: "Success",
        detail: "Blog post deleted successfully",
        life: 3000,
      });
      await fetchPosts(); // Refresh the posts list
    } catch (error) {
      console.error("Error deleting post:", error);
      toast.current?.show({
        severity: "error",
        summary: "Error",
        detail: "Failed to delete blog post",
        life: 3000,
      });
    }
    setShowDeleteDialog(false);
  };

  if (loading) {
    return <div>Loading...</div>;
  }
  return (
    <div className="blogs-container">
      <h1 className="blogs-title">Blogs</h1>
      <div className="blogs-grid">
        {posts.map((post) => (
          <Card
            key={post._id}
            className="blog-card"
            onClick={() => handleBlogClick(post._id)}
          >
            {post.imageUrl && (
              <div className="blog-image-container">
                <img
                  src={post.imageUrl}
                  alt={post.title}
                  className="blog-image"
                />
              </div>
            )}
            <div className="blog-content">
              <h3 className="blog-title">{post.title}</h3>

              {user && (
                <div className="blog-actions">
                  <Button
                    icon="pi pi-pencil"
                    className="p-button-rounded p-button-success mr-2"
                    onClick={(e) => handleEdit(e, post._id)}
                  />
                  <Button
                    icon="pi pi-trash"
                    className="p-button-rounded p-button-danger"
                    onClick={(e) => confirmDelete(e, post._id)}
                  />
                </div>
              )}
            </div>
          </Card>
        ))}
      </div>

      <Toast ref={toast} />

      <ConfirmDialog
        visible={showDeleteDialog}
        onHide={() => setShowDeleteDialog(false)}
        message="Are you sure you want to delete this blog post?"
        header="Confirm Delete"
        icon="pi pi-exclamation-triangle"
        accept={handleDelete}
        reject={() => setShowDeleteDialog(false)}
      />
    </div>
  );
};

export default Blog;
