import React, { useState, useRef, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { InputText } from "primereact/inputtext";
import {
  InputNumber,
  InputNumberValueChangeEvent,
} from "primereact/inputnumber";
import { Dropdown } from "primereact/dropdown";
import { InputTextarea } from "primereact/inputtextarea";
import { Button } from "primereact/button";
import { Toast } from "primereact/toast";
import { ProgressSpinner } from "primereact/progressspinner";
import { RadioButton } from "primereact/radiobutton";
import { Checkbox } from "primereact/checkbox";
import { cloudinaryConfig } from "../config/cloudinary";
import "../CSS/AddRealEstate.css";
import axios from "axios";

interface RealEstate {
  title: string;
  description: string;
  price: number;
  location: string;
  propertyType: string;
  bedrooms: number;
  bathrooms: number;
  area: number;
  mainImage: string;
  galleryImages: string[];
  youtubeUrl?: string;
  country: "Lebanon" | "Abidjan";
  type: "realestate";
  showInFlipcard: boolean;
}

const EditRealEstate: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const toast = useRef<Toast>(null);
  const [loading, setLoading] = useState(false);
  const [mainImage, setMainImage] = useState<File | null>(null);
  const [newGalleryImages, setNewGalleryImages] = useState<File[]>([]);
  const [mainImageUrl, setMainImageUrl] = useState("");
  const [galleryUrls, setGalleryUrls] = useState<string[]>([]);
  const [youtubeUrl, setYoutubeUrl] = useState("");
  const [showInFlipcard, setShowInFlipcard] = useState(false);

  const [formData, setFormData] = useState<RealEstate>({
    title: "",
    description: "",
    price: 0,
    location: "",
    propertyType: "",
    bedrooms: 0,
    bathrooms: 0,
    area: 0,
    mainImage: "",
    galleryImages: [],
    youtubeUrl: "",
    country: "Lebanon",
    type: "realestate",
    showInFlipcard: false,
  });

  const propertyTypes = [
    { label: "Apartment", value: "apartment" },
    { label: "House", value: "house" },
    { label: "Villa", value: "villa" },
    { label: "Office", value: "office" },
    { label: "Land", value: "land" },
    { label: "Other", value: "other" },
  ];

  useEffect(() => {
    const fetchProperty = async () => {
      if (!id) return;

      try {
        setLoading(true);

        //using axios
        const response = await axios.get(
          process.env.REACT_APP_API + `/real-estates/${id}`
        );
        const data = response.data as RealEstate;
        setFormData({
          title: data.title || "",
          description: data.description || "",
          price: data.price || 0,
          location: data.location || "",
          propertyType: data.propertyType || "",
          bedrooms: data.bedrooms || 0,
          bathrooms: data.bathrooms || 0,
          area: data.area || 0,
          mainImage: data.mainImage || "",
          galleryImages: data.galleryImages || [],
          youtubeUrl: data.youtubeUrl || "",
          country: data.country || "Lebanon",
          type: data.type || "realestate",
          showInFlipcard: data.showInFlipcard || false,
        });
        setMainImageUrl(data.mainImage || "");
        setGalleryUrls(data.galleryImages || []);
        setYoutubeUrl(data.youtubeUrl || "");
        setShowInFlipcard(data.showInFlipcard || false);
      } catch (error) {
        console.error("Error fetching property:", error);
        toast.current?.show({
          severity: "error",
          summary: "Error",
          detail: "Failed to fetch property details",
        });
      } finally {
        setLoading(false);
      }
    };

    fetchProperty();
  }, [id]);

  const handleInputChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleDropdownChange = (e: { value: string }) => {
    setFormData((prev) => ({
      ...prev,
      propertyType: e.value,
    }));
  };

  const handleNumberChange = (
    field: string,
    value: number | null | undefined
  ) => {
    setFormData((prev) => ({
      ...prev,
      [field]: value ?? 0,
    }));
  };

  const handleMainImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      const file = e.target.files[0];
      setMainImage(file);
      setMainImageUrl(URL.createObjectURL(file));
    }
  };

  const handleGalleryImagesChange = (
    e: React.ChangeEvent<HTMLInputElement>
  ) => {
    if (e.target.files) {
      const files = Array.from(e.target.files);
      setNewGalleryImages(files);
    }
  };

  const uploadImage = async (file: File) => {
    try {
      const formData = new FormData();
      formData.append("file", file);
      formData.append("upload_preset", cloudinaryConfig.uploadPreset);
      formData.append("cloud_name", cloudinaryConfig.cloudName);

      const response = await fetch(
        `https://api.cloudinary.com/v1_1/${cloudinaryConfig.cloudName}/image/upload`,
        {
          method: "POST",
          body: formData,
        }
      );

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error?.message || "Failed to upload image");
      }

      const data = await response.json();
      return data.secure_url;
    } catch (error: any) {
      console.error("Error uploading image:", error);
      throw new Error(`Error uploading image: ${error.message}`);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!id) return;

    try {
      setLoading(true);

      let updatedMainImageUrl = mainImageUrl;
      if (mainImage) {
        updatedMainImageUrl = await uploadImage(mainImage);
      }

      const updatedGalleryUrls = [...galleryUrls];
      if (newGalleryImages.length > 0) {
        const newUrls = await Promise.all(
          newGalleryImages.map((file) => uploadImage(file))
        );
        updatedGalleryUrls.push(...newUrls);
      }

      //todo: check if the property is already in the flipcard
      // If enabling flipcard, check the limit
      // if (showInFlipcard) {
      //   const flipcardQuery = query(
      //     collection(db, "realEstate"),
      //     where("showInFlipcard", "==", true)
      //   );
      //   const querySnapshot = await getDocs(flipcardQuery);
      //   const currentFlipCards = querySnapshot.docs.filter(
      //     (doc) => doc.id !== id
      //   );

      //   if (currentFlipCards.length >= 5) {
      //     toast.current?.show({
      //       severity: "error",
      //       summary: "Error",
      //       detail:
      //         "Maximum limit of 5 flipcards reached. Please uncheck some posts first.",
      //       life: 5000,
      //     });
      //     setShowInFlipcard(false);
      //     return;
      //   }
      // }

      const updatedData = {
        ...formData,
        mainImage: updatedMainImageUrl,
        galleryImages: updatedGalleryUrls,
        youtubeUrl,
        showInFlipcard,
        updatedAt: Date.now(),
      };


      await axios.put(
        process.env.REACT_APP_API + `/real-estates/${id}`,
        updatedData
      );

      toast.current?.show({
        severity: "success",
        summary: "Success",
        detail: "Real Estate updated successfully",
        life: 3000,
      });

      navigate(
        formData.country === "Lebanon"
          ? "/lebanon-realestate"
          : "/abidjan-realestate"
      );
    } catch (error) {
      console.error("Error updating property:", error);
      toast.current?.show({
        severity: "error",
        summary: "Error",
        detail: "Failed to update property",
      });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="add-property-container">
      <h1>Edit Property</h1>
      <form onSubmit={handleSubmit} className="property-form">
        <div className="form-section">
          <label htmlFor="title">Title</label>
          <InputText
            id="title"
            name="title"
            value={formData.title}
            onChange={handleInputChange}
            required
          />
        </div>

        <div className="form-section">
          <label htmlFor="description">Description</label>
          <InputTextarea
            id="description"
            name="description"
            value={formData.description}
            onChange={handleInputChange}
            rows={5}
            required
          />
        </div>

        <div className="form-row">
          <div className="form-section">
            <label htmlFor="price">Price ($)</label>
            <InputNumber
              id="price"
              value={formData.price}
              onValueChange={(e: InputNumberValueChangeEvent) =>
                handleNumberChange("price", e.value)
              }
              mode="currency"
              currency="USD"
              required
            />
          </div>

          <div className="form-section">
            <label htmlFor="location">Location</label>
            <InputText
              id="location"
              name="location"
              value={formData.location}
              onChange={handleInputChange}
              required
            />
          </div>
        </div>

        <div className="form-row">
          <div className="form-section">
            <label htmlFor="propertyType">Property Type</label>
            <Dropdown
              id="propertyType"
              value={formData.propertyType}
              options={propertyTypes}
              onChange={handleDropdownChange}
              placeholder="Select Property Type"
              required
            />
          </div>

          <div className="form-section">
            <label htmlFor="area">Area (sqft)</label>
            <InputNumber
              id="area"
              value={formData.area}
              onValueChange={(e: InputNumberValueChangeEvent) =>
                handleNumberChange("area", e.value)
              }
              required
            />
          </div>
        </div>

        <div className="form-row">
          <div className="form-section">
            <label htmlFor="bedrooms">Bedrooms</label>
            <InputNumber
              id="bedrooms"
              value={formData.bedrooms}
              onValueChange={(e: InputNumberValueChangeEvent) =>
                handleNumberChange("bedrooms", e.value)
              }
              required
            />
          </div>

          <div className="form-section">
            <label htmlFor="bathrooms">Bathrooms</label>
            <InputNumber
              id="bathrooms"
              value={formData.bathrooms}
              onValueChange={(e: InputNumberValueChangeEvent) =>
                handleNumberChange("bathrooms", e.value)
              }
              required
            />
          </div>
        </div>

        <div className="form-section">
          <h3>Location Details</h3>
          <div className="field">
            <label htmlFor="country">Country</label>
            <div className="radio-group">
              <div className="radio-item">
                <RadioButton
                  inputId="Lebanon"
                  name="country"
                  value="Lebanon"
                  onChange={(e) =>
                    setFormData((prev) => ({ ...prev, country: e.value }))
                  }
                  checked={formData.country === "Lebanon"}
                />
                <label htmlFor="Lebanon" className="radio-label">
                  Lebanon
                </label>
              </div>
              <div className="radio-item">
                <RadioButton
                  inputId="Abidjan"
                  name="country"
                  value="Abidjan"
                  onChange={(e) =>
                    setFormData((prev) => ({ ...prev, country: e.value }))
                  }
                  checked={formData.country === "Abidjan"}
                />
                <label htmlFor="Abidjan" className="radio-label">
                  Abidjan
                </label>
              </div>
            </div>
          </div>
        </div>

        <div className="form-section">
          <label htmlFor="youtubeUrl">YouTube Video URL (Optional)</label>
          <InputText
            id="youtubeUrl"
            value={youtubeUrl}
            onChange={(e) => setYoutubeUrl(e.target.value)}
            placeholder="Enter YouTube video URL"
          />
        </div>

        <div className="form-section">
          <label htmlFor="mainImage">Main Image</label>
          <input
            type="file"
            id="mainImage"
            accept="image/*"
            onChange={handleMainImageChange}
          />
          {mainImageUrl && (
            <img
              src={mainImageUrl}
              alt="Main preview"
              className="image-preview"
            />
          )}
        </div>

        <div className="form-section">
          <label>Existing Gallery Images</label>
          <div className="gallery-previews">
            {galleryUrls.map((url, index) => (
              <div
                key={`existing-${index}`}
                className="gallery-preview-container"
              >
                <img
                  src={url}
                  alt={`Gallery ${index + 1}`}
                  className="gallery-preview"
                />
              </div>
            ))}
          </div>
        </div>

        <div className="form-section">
          <label htmlFor="galleryImages">Add New Gallery Images</label>
          <input
            type="file"
            id="galleryImages"
            accept="image/*"
            multiple
            onChange={handleGalleryImagesChange}
          />
          <div className="gallery-previews">
            {newGalleryImages.map((file, index) => (
              <div key={`new-${index}`} className="gallery-preview-container">
                <img
                  src={URL.createObjectURL(file)}
                  alt={`New gallery preview ${index + 1}`}
                  className="gallery-preview"
                />
              </div>
            ))}
          </div>
        </div>

        <div
          className="p-field-checkbox"
          style={{ marginTop: "1rem", marginBottom: "1rem" }}
        >
          <Checkbox
            inputId="showInFlipcard"
            checked={showInFlipcard}
            onChange={(e) => setShowInFlipcard(e.target.checked ?? false)}
          />
          <label
            htmlFor="showInFlipcard"
            className="p-checkbox-label"
            style={{ marginLeft: "0.5rem" }}
          >
            Show in Home Flipcard Section
          </label>
        </div>

        <Button
          type="submit"
          label={loading ? "Updating Property..." : "Update Property"}
          icon="pi pi-check"
          disabled={loading}
        />
        {loading && (
          <ProgressSpinner style={{ width: "50px", height: "50px" }} />
        )}
      </form>

      <Toast ref={toast} />
    </div>
  );
};

export default EditRealEstate;
